            <div id="slide-changer">
              <div id="slide-1" v-on:click="scrollTo(null,null,'about')" class="selected"></div>
              <div id="slide-2" v-on:click="scrollTo(null,null,'work')"></div>
              <div id="slide-3" v-on:click="scrollTo(null,null,'contact')"></div>
