<script>
import { EventBus } from '@/services/Bus.js';

export default {
  name: 'Home',
  props: {
    msg: String
  },
  data: function(){
    return {
      slide1: null,
      slide2: null,
      slide3:null,
      selectedSlide: null,
      statsVisible: false,
      isScrolling: false,
      isAtTop: true,
      slideArray: ['about','work','contact'],
      currentSlide: null,
      scrollPos: 0,
      counter: 0,
      carouseIsVisible: false,
      atBottom: false
    }
  },
  methods: {
    scrollTo(loc,id,element){
        //this.$router.push({path:'/'});
        
        if(element != 'carousel-wrapper' && element != 'home'){
          this.selectedSlide.classList.remove('selected');
        }
        
        switch(element){
          case 'home':
            this.atBottom = false;
            break;
          case 'carousel-wrapper':
            this.atBottom = true;
            break;
          case 'about':
            this.currentSlide ='about';
            this.selectedSlide = this.slide1;
            this.counter = 0;
            this.atBottom = true;
            this.selectedSlide.classList.add('selected');
            break;
          case 'work':
            this.currentSlide ='work';
            this.selectedSlide = this.slide2;
            this.counter = 1;
            this.atBottom = true;
            this.selectedSlide.classList.add('selected');
            break;
          case 'contact':
            this.currentSlide ='contact';
            this.selectedSlide = this.slide3;
            this.counter = 2;
            this.atBottom = true;
            this.selectedSlide.classList.add('selected');
            break;
        }


        var carousel = document.getElementById('carousel-wrapper');

        if(loc == 'preScroll'){
          carousel.scrollLeft = window.innerWidth * id;
          carousel.scrollIntoView({
            behavior: 'smooth'
          });
          
        }else{
          switch(element){
            case 'home':
              window.scroll({
                top: 0, 
                behavior: 'smooth'
              });
              break;
            case 'carousel-wrapper':
              window.scroll({
                top: window.innerHeight, 
                behavior: 'smooth'
              });
              break;
            case 'about':
              carousel.scroll({
                left: 0, 
                behavior: 'smooth'
              });
              break;
            case 'work':
              carousel.scroll({
                left: window.innerWidth, 
                behavior: 'smooth'
              });
              break;
            case 'contact':
              carousel.scroll({
                left: window.innerWidth * 2, 
                behavior: 'smooth'
              });
              break;
          }
        }
        
    },
    toggleStats(){
      if(this.statsVisible){
        document.getElementsByClassName('stats')[0].style.height = "0em";
        document.getElementsByClassName('stats')[0].style.border = "none";
        this.statsVisible = false;
      }else{
        document.getElementsByClassName('stats')[0].style.height = "29em";
        document.getElementsByClassName('stats')[0].style.borderBottom = "5px solid rgba(255, 193, 47, 0.712) ";
         document.getElementsByClassName('stats')[0].style.borderTop = "5px solid rgba(255, 193, 47, 0.712) ";
        this.statsVisible = true;
      }
    },
    handleScroll() {
      // Any code to be executed when the window is scrolled
      this.scrollPos = window.scrollY;

      
      if(window.scrollY >= document.getElementById('about').clientHeight){
        document.getElementById('home').style.display = "none";
      }else{
        document.getElementById('home').style.display = "block";
      }

      if(window.scrollY >= window.innerHeight/2.2){
        document.getElementById('scrollAlert').style.display = "none";
      }else{
        document.getElementById('scrollAlert').style.display = "block";
      }
      this.showSlideChanger();
      document.getElementById('home').style.opacity = ((window.innerHeight - 50) - window.scrollY) / (window.innerHeight - 50);
    },
    showSlideChanger(){

      if(Math.ceil(window.scrollY)  <= Math.ceil(window.innerHeight) + 1 && Math.ceil(window.scrollY)  >= Math.ceil(window.innerHeight) - 1){
        document.getElementById('slide-changer').style.display = 'block';
        document.getElementById('about').style.overflowY = "scroll";
        this.carouseIsVisible = true;
      }else{
        document.getElementById('slide-changer').style.display = 'none';
        document.getElementById('about').style.overflowY = "hidden"; 
        this.carouseIsVisible = false;     
      }
      
      if(Math.ceil(window.scrollY)  <= Math.ceil(window.innerHeight) + 1 && Math.ceil(window.scrollY)  >= Math.ceil(window.innerHeight) - 1){
        this.isAtTop = false;
      }else{
        this.isAtTop = true;
      }
    },
    scrollDirection(e){

      if (this.scrollPos == 0  && e.deltaY > 0 && this.isAtTop) {
        this.isAtTop = false;
        this.scrollTo(null,null,'carousel-wrapper');
      }
      if (document.getElementById(this.currentSlide).scrollTop == 0 && e.deltaY < 0 && !this.isAtTop){
        this.isAtTop = true;
        this.scrollTo(null,null,'home');
      }
    },
    vtouchHomeScroll(direction){
      if(direction == 'top'){
        this.scrollTo(null,null,'carousel-wrapper');
      }else if(direction == 'left'){
        //open side nav
        EventBus.$emit("open.sidenav", location);
      }
    },
    vtouchCarouselScroll(direction){
      if(direction == 'bottom'&& document.getElementById(this.currentSlide).scrollTop == 0 ){
        this.scrollTo(null,null,'home');
      }else if(direction == 'left'){
        if(this.counter != 2){
          this.scrollTo(null,null, this.slideArray[this.counter + 1]);
        }
      }else if(direction == 'right'){
        if(this.counter != 0){
          this.scrollTo(null,null,this.slideArray[this.counter - 1]);
        }
      }
    },
    resizeSnap(){
      
      if(this.atBottom){
         window.scroll({
          top: window.innerHeight
        });
      }else{
        window.scroll({
          top: 0
        });
      }
      document.getElementById('carousel-wrapper').scroll({
        left: window.innerWidth * this.counter
      });
    }
  },
  mounted(){
     //document.getElementById('home').style.color = "red";
     if(Math.ceil(window.scrollY)  <= Math.ceil(window.innerHeight) + 1 && Math.ceil(window.scrollY)  >= Math.ceil(window.innerHeight) - 1){
        document.getElementById('home').style.display = "none";
        this.atBottom = true;
      }
      this.showSlideChanger();
      this.slide1 = document.getElementById('slide-1');
      this.slide2 = document.getElementById('slide-2');
      this.slide3 = document.getElementById('slide-3');
      this.selectedSlide = this.slide1;
      this.currentSlide ='about';
      this.selectedSlide.classList.add('selected');
      window.scroll({
        top: 0, 
        left: 0
      });
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize', this.resizeSnap);
    EventBus.$on('nav.to', data => {
      var carousel = document.getElementById('carousel-wrapper');
      switch(data){
            case 'home':
              this.scrollTo(null,null,'home');
              break;
            case 'about':
              if(this.scrollPos > window.innerHeight -10){
                this.scrollTo(null,null,'about');
              }else { 
                  carousel.scrollLeft = window.innerWidth * 0;
                  carousel.scrollIntoView({
                    behavior: 'smooth'
                  });
              }
              break;
            case 'work':
              if(this.scrollPos > window.innerHeight -10){
                this.scrollTo(null,null,'work');
              }else { 
                  carousel.scrollLeft = window.innerWidth * 1;
                  carousel.scrollIntoView({
                    behavior: 'smooth'
                  });
              }
              break;
            case 'contact':
              if(this.scrollPos > window.innerHeight -10){
                this.scrollTo(null,null,'contact');
              }else {
                  carousel.scrollLeft = window.innerWidth * 2;
                  carousel.scrollIntoView({
                    behavior: 'smooth'
                  });
              }
              break;
          }
    });
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.resizeSnap);
  }
}
</script>

